'use client';

import { useForm, type SubmitHandler } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { useToast } from '@/hooks/use-toast';
import { useRouter } from 'next/navigation';
import { Save, Loader2, ArrowLeft, UserCog, ShieldAlert } from 'lucide-react';
import { useState, useEffect } from 'react';
import { useAuth } from '@/contexts/AuthContext';
import { updateUserProfileAction } from '@/app/actions/user-actions';
import type { UserProfileUpdatePayload } from '@/lib/types';

const profileSchema = z.object({
  nome: z.string().min(3, { message: 'Nome deve ter pelo menos 3 caracteres.' }),
  email: z.string().email({ message: 'E-mail inválido.' }),
  whatsapp: z.string().regex(/^\d{10,11}$/, { message: 'WhatsApp inválido (somente números, DDD+Número).' }),
  currentPassword: z.string().optional(),
  newPassword: z.string().min(6, { message: 'Nova senha deve ter pelo menos 6 caracteres.' }).optional().or(z.literal('')),
  confirmNewPassword: z.string().optional(),
})
.refine(data => {
    if (data.newPassword && !data.currentPassword) {
        return false;
    }
    return true;
}, {
    message: 'Senha atual é obrigatória para definir uma nova senha.',
    path: ['currentPassword'],
})
.refine(data => data.newPassword === data.confirmNewPassword, {
  message: 'As novas senhas não coincidem.',
  path: ['confirmNewPassword'],
});

type ProfileFormValues = z.infer<typeof profileSchema>;

export default function OrganizerProfilePage() {
  const { toast } = useToast();
  const router = useRouter();
  const { organizerUser, organizerLogin, isLoading: authIsLoading } = useAuth();
  const [isSubmitting, setIsSubmitting] = useState(false);

  const form = useForm<ProfileFormValues>({
    resolver: zodResolver(profileSchema),
    defaultValues: {
      nome: '',
      email: '',
      whatsapp: '',
      currentPassword: '',
      newPassword: '',
      confirmNewPassword: '',
    },
  });

  const { register, handleSubmit, formState: { errors }, reset, setError: setFormError } = form;

  useEffect(() => {
    if (!authIsLoading && organizerUser) {
      reset({
        nome: organizerUser.nome,
        email: organizerUser.email,
        whatsapp: organizerUser.whatsapp || '',
      });
    } else if (!authIsLoading && !organizerUser) {
        router.replace('/login?redirect=/organizer/dashboard');
    }
  }, [authIsLoading, organizerUser, reset, router]);

  if (authIsLoading || !organizerUser) {
    return (
      <div className="flex items-center justify-center min-h-[calc(100vh-200px)]">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
        <p className="ml-2">Carregando perfil...</p>
      </div>
    );
  }

  const onSubmit: SubmitHandler<ProfileFormValues> = async (data) => {
    if (!organizerUser?.id) {
      toast({ title: 'Erro de Autenticação', description: 'ID do organizador não encontrado.', variant: 'destructive' });
      return;
    }
    setIsSubmitting(true);

    const payload: UserProfileUpdatePayload = {
      nome: data.nome,
      email: data.email,
      whatsapp: data.whatsapp,
    };

    if (data.newPassword) {
      payload.currentPassword = data.currentPassword;
      payload.newPassword = data.newPassword;
    }

    const result = await updateUserProfileAction(organizerUser.id, payload);
    setIsSubmitting(false);

    if (result.success && result.user) {
      toast({
        title: 'Perfil Atualizado!',
        description: 'Suas informações foram atualizadas com sucesso.',
        variant: 'default',
      });
      organizerLogin(result.user); // Update AuthContext state and localStorage
      reset({ // Reset form with new data, clear password fields
        nome: result.user.nome,
        email: result.user.email,
        whatsapp: result.user.whatsapp || '',
        currentPassword: '',
        newPassword: '',
        confirmNewPassword: '',
      });
    } else {
      toast({
        title: 'Falha ao Atualizar Perfil',
        description: result.message,
        variant: 'destructive',
      });
      if (result.message.toLowerCase().includes("senha atual incorreta")) {
        setFormError("currentPassword", { type: "server", message: result.message });
      }
    }
  };

  return (
    <div className="max-w-2xl mx-auto py-8">
      <Button variant="outline" onClick={() => router.push('/organizer/dashboard')} className="mb-6">
        <ArrowLeft className="mr-2 h-4 w-4" /> Voltar para o Painel
      </Button>
      <Card className="shadow-xl">
        <CardHeader>
          <CardTitle className="text-3xl text-primary flex items-center gap-2">
            <UserCog className="h-8 w-8" /> Editar Perfil de Organizador
          </CardTitle>
          <CardDescription>
            Atualize suas informações pessoais e senha.
          </CardDescription>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
            <div>
              <Label htmlFor="nome">Nome da Empresa/Organizador</Label>
              <Input id="nome" {...register('nome')} className="mt-1" />
              {errors.nome && <p className="text-sm text-destructive mt-1">{errors.nome.message}</p>}
            </div>

            <div>
              <Label htmlFor="email">E-mail de Contato</Label>
              <Input id="email" type="email" {...register('email')} className="mt-1" />
              {errors.email && <p className="text-sm text-destructive mt-1">{errors.email.message}</p>}
            </div>

            <div>
              <Label htmlFor="whatsapp">WhatsApp</Label>
              <Input id="whatsapp" type="tel" {...register('whatsapp')} className="mt-1" placeholder="Ex: 11988887777" />
              {errors.whatsapp && <p className="text-sm text-destructive mt-1">{errors.whatsapp.message}</p>}
            </div>

            <Card className="p-4 border-dashed border-primary/50">
              <h3 className="text-lg font-semibold text-primary mb-2 flex items-center gap-1.5"><ShieldAlert size={20}/> Alterar Senha</h3>
              <p className="text-sm text-muted-foreground mb-4">Deixe em branco se não deseja alterar a senha.</p>
              <div className="space-y-4">
                <div>
                  <Label htmlFor="currentPassword">Senha Atual</Label>
                  <Input id="currentPassword" type="password" {...register('currentPassword')} className="mt-1" autoComplete="current-password" />
                  {errors.currentPassword && <p className="text-sm text-destructive mt-1">{errors.currentPassword.message}</p>}
                </div>
                <div>
                  <Label htmlFor="newPassword">Nova Senha</Label>
                  <Input id="newPassword" type="password" {...register('newPassword')} className="mt-1" autoComplete="new-password" />
                  {errors.newPassword && <p className="text-sm text-destructive mt-1">{errors.newPassword.message}</p>}
                </div>
                <div>
                  <Label htmlFor="confirmNewPassword">Confirmar Nova Senha</Label>
                  <Input id="confirmNewPassword" type="password" {...register('confirmNewPassword')} className="mt-1" autoComplete="new-password" />
                  {errors.confirmNewPassword && <p className="text-sm text-destructive mt-1">{errors.confirmNewPassword.message}</p>}
                </div>
              </div>
            </Card>


            <Button type="submit" className="w-full bg-primary hover:bg-primary/90 text-primary-foreground font-semibold" disabled={isSubmitting}>
              {isSubmitting ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Save className="mr-2 h-4 w-4" />}
              Salvar Alterações
            </Button>
          </form>
        </CardContent>
      </Card>
    </div>
  );
}
